.coin-container{
    display: flex;
    justify-content: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin{
    display: flex;
    padding-right: 24px;
    align-items: center;
    min-width: 300px;
}
.coin h1{
    font-size: 16px ;
    width: 50px;

}

.coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;

}
.coin-symbol{
    text-transform: uppercase;

}
.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    
    width: 200px;

}

.coin-marketcap{
    width: 230px;

}

.coin-percent{
    width: 80%;
}

.red{
    color: #f00606;
}


.green{
    color: #11d811;
}